@import "https://fonts.googleapis.com/css2?family=Inconsolata:wght@300;400;500&display=swap";
@import "https://fonts.googleapis.com/css2?family=Paytone+One&display=swap";

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

select {
  outline: none;
  appearance: none;
  border: none;
}

input[type="text"] {
  outline: none;
  border: none;
}

::-webkit-scrollbar {
  width: 15px;
  background-color: #3c3e44;
}

::-webkit-scrollbar-thumb {
  background-color: #202329;
  border-radius: 9em;
  box-shadow: inset 1px 1px 10px #fff;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #202329;
}

body {
  font-family: Inconsolata, monospace;
  max-height: 100vh;
  overflow: hidden;
}

.app {
  max-height: 100vh;
  overflow-y: scroll;
}

.app__overlay::before {
  content: "";
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: #201f1f65;
  z-index: 3;
}

@media (max-width: 1020px) {
  ::-webkit-scrollbar {
    width: 0;
  }
}
